import './Login.less'
import logo from '@/assets/imgs/logo.JPG'
import { Tabs, Button, Form, Input } from 'antd'
import { Link, useNavigate } from 'react-router-dom'
import { useRef, useState } from 'react'

const layout = {
  labelCol: { span: 5 },
  wrapperCol: { span: 16 },
}

const tailLayout = {
  wrapperCol: { offset: 5, span: 16 },
}

const tabList = [
  {
    label: '',
    key: 'pwd',
    children: '',
  },
  {
    label: '',
    key: 'msg',
    children: '',
  },
]

const Login = () => {
  const [tabKey, setTabKey] = useState('pwd')
  const formRef = useRef(null)

  const navigate = useNavigate()
  const onFinish = (values) => {
    console.log(values)
    navigate('/', { state: 111 })
  }
  const onChange = (key) => {
    setTabKey(key)
  }

  return (
    <div className="login-container">
      <Link className="logo-container" to="/">
        <img className="logo-img" src={logo} alt="" />
      </Link>
      <Tabs
        onChange={onChange}
        type="card"
        items={tabList.map((item) => {
          return {
            label: item.label,
            key: item.key,
            children: '',
          }
        })}
      />
      <Form
        {...layout}
        ref={formRef}
        name="control-ref"
        onFinish={onFinish}
        style={{ maxWidth: 600 }}
      >
        {tabKey === 'pwd' ? <PwdForm /> : <MsgForm />}
        <Form.Item {...tailLayout}>
          <p>
            
            <Link className="link-style" to="https://www.baidu.com">
              
            </Link>
            
            <Link className="link-style" to="https://www.baidu.com">
              
            </Link>
          </p>
        </Form.Item>
        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit" className="login-btn">
            
          </Button>
          <p>
            <Link className="link-style" to="/register">
              
            </Link>
            <Link className="link-style" to="/forget-pwd">
              
            </Link>
          </p>
        </Form.Item>
      </Form>
    </div>
  )
}

const PwdForm = () => {
  return (
    <div>
      <Form.Item
        name="phoneNo"
        label=""
        rules={[{ required: true, message: '/Email/' }]}
      >
        <Input placeholder="/Email/" />
      </Form.Item>
      <Form.Item
        name="pwd"
        label=""
        rules={[{ required: true, message: '' }]}
      >
        <Input.Password />
      </Form.Item>
      <Form.Item name="code" label="" rules={[{ required: true, message: '' }]}>
        <Input />
      </Form.Item>
    </div>
  )
}

const MsgForm = () => {
  return (
    <div>
      <Form.Item
        name="phoneNo"
        label=""
        rules={[{ required: true, message: '' }]}
      >
        <Input placeholder="" />
      </Form.Item>
      <Form.Item name="code" label="" rules={[{ required: true, message: '' }]}>
        <Input />
      </Form.Item>
      <Form.Item
        name="phoneCode"
        label=""
        rules={[{ required: true, message: '' }]}
      >
        <Input />
      </Form.Item>
    </div>
  )
}

export default Login
