import './PersonalInfoIntro.less'
import { EnvironmentOutlined } from '@ant-design/icons'
import { Space } from 'antd'
import PersonalAvatar from '@/pages/components/personalAvatar/PersonalAvatar'
import TagList from '@/pages/components/tagList/TagList'

const PersonalInfoIntro = () => {
  return (
    <div className="personal-info-tro">
      <PersonalAvatar />
      <div className="personal-introduce">
        <p className="name">
          <b></b>
        </p>
        <div className="desinger-level">
          <TagList
            consistentColor={{ backgroundColor: '#009836', color: '#fff' }}
            tagList={[{ text: 'D' }]}
          />
        </div>
        <div>
          <Space>
            <EnvironmentOutlined />
            
          </Space>
        </div>
        <div className="experience">
          <span className="ex-label"></span>
          <span className="ex-direction">APP,</span>
          <span className="ex-time-label"></span>
          <span className="ex-time">10</span>
        </div>
        <pre className="desinger-desc">
          110UIAPPB/UI
          2
          3sketchFigmaMasterGoPhotoshopiIIustratorprotopieAxure
          RP9div+cssHTML5+css3JavaScriptjquery
        </pre>
      </div>
    </div>
  )
}

export default PersonalInfoIntro
