/**
 * tag
 * @desc 12
 *       hoveractiveModetruehover[]taghover
 * @param {Object} [tagConfig] - 
 * @param {Object} [singleData] - 
 * @param {Function} [setActiveIndexF] - 
 * @param {Number} [currIndex] - taglist
 * @param {Number} [activeIndex] - 
 **/

import './SingleTag.less'
import { Tag } from 'antd'
import { useEffect, useState } from 'react'

const SingleTag = ({ tagConfig, singleData, setActiveIndexF, currIndex, activeIndex }) => {
  const [colorConfig, setColorConfig] = useState(tagConfig.consistentColor) // 
  const { backgroundColor, color } = singleData

  useEffect(() => {
    // singlesingle
    let temp = {}
    if (backgroundColor) {
      temp['backgroundColor'] = backgroundColor
    }
    if (color) {
      temp['color'] = color
    }
    if (Object.keys(temp).length) setColorConfig(temp)

    if (currIndex === activeIndex && tagConfig.activeMode) {
      // activeModetaghover
      setColorConfig(tagConfig.activeColor)
    } else {
      setColorConfig(tagConfig.consistentColor)
    }
  }, [activeIndex])

  const overTag = () => {
    if (!tagConfig.activeMode) return
    setColorConfig(tagConfig.activeColor)
  }
  const outTag = () => {
    if (!tagConfig.activeMode) return
    if (currIndex === activeIndex) return // tag
    setColorConfig(tagConfig.consistentColor)
  }
  const chosen = () => {
    if (!tagConfig.activeMode) return
    setActiveIndexF(currIndex)
  }
  return (
    <Tag
      className="tag-single-style"
      style={{ ...colorConfig }}
      onMouseOver={overTag}
      onMouseOut={outTag}
      onClick={chosen}
    >
      {singleData.text}
    </Tag>
  )
}

export default SingleTag
