import './WorkListDesignerInfo.less'
import {
  EnvironmentOutlined,
  UserOutlined,
  TrademarkOutlined,
  PlusOutlined,
  MailOutlined,
  QuestionCircleOutlined,
} from '@ant-design/icons'
import { Form, Input, Space } from 'antd'
import CommonButton from '@/pages/components/commonButton/CommonButton'
import PersonalAvatar from '@/pages/components/personalAvatar/PersonalAvatar'
const { TextArea } = Input

const WorkListDesignerInfo = () => {
  const onFinish = (values) => {
    console.log('Success:', values)
  }

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo)
  }
  const addFocus = (e) => {
    console.log('Failed:', e)
  }
  const consulting = (e) => {
    console.log('Failed:', e)
  }
  const getPrice = (e) => {
    console.log('Failed:', e)
  }
  return (
    <div className="work-list-designer-info">
      <div className="information-1">
        <div className="avatar-container">
          <PersonalAvatar />
          <ul className="desiner-introduce">
            <li className="desiner-introduce-li desiner-introduce-name">
              <b></b>
              <span className="desiner-introduce-name-icon">
                <UserOutlined />
                <TrademarkOutlined />
              </span>
            </li>
            <li className="desiner-introduce-li"></li>
            <li className="desiner-introduce-li">
               <EnvironmentOutlined />
            </li>
          </ul>
        </div>
        <div className="designer-info-action-btn">
          <div className="add-focus-btn">
            <CommonButton
              padNumber="30"
              size="large"
              fontSize="16"
              setIcon={<PlusOutlined />}
              btnClicked={addFocus}
              title=""
              emphasize
            />
          </div>
          <div className="consulting-btn">
            <CommonButton
              padNumber="30"
              size="large"
              fontSize="16"
              setIcon={<MailOutlined />}
              btnClicked={consulting}
              title=""
            />
          </div>
        </div>
        <ul className="correlation-index">
          <li className="correlation--index-label">
            <span></span>
            <p>
              <span className="correlation-index-area">
                 <span className="correlation-index-val">36</span>
              </span>
              <span className="correlation-index-area">
                 <span className="correlation-index-val">4</span>
              </span>
            </p>
          </li>
          <li className="correlation--index-label">
            <span></span>
            <p className="correlation-index-val">1160153</p>
          </li>
          <li className="correlation--index-label">
            <span></span>
            <p className="correlation-index-val">218</p>
          </li>
          <li className="correlation--index-label">
            <span></span>
            <p className="correlation-index-val">234</p>
          </li>
          <li className="correlation--index-label">
            <span></span>
            <p className="correlation-index-val">53</p>
          </li>
          <li className="correlation--index-label">
            <span className="emphasis-label"></span>
            <p className="correlation-index-val">4</p>
          </li>
          <li className="correlation--index-label">
            <span className="emphasis-label">
              <Space size="small">
                
                <QuestionCircleOutlined />
              </Space>
            </span>
            <p className="correlation-index-val">12</p>
          </li>
        </ul>
        <p className="identity-activation-date">
          <span></span>
          <span className="identity-label"> </span>
        </p>
        <p className="identity-activation-date activation-date-detail">
          <span className="activation-date">2015-08-08</span>
          <span></span>
        </p>
      </div>
      <div className="information-2">
        <Form onFinish={onFinish} onFinishFailed={onFinishFailed} autoComplete="off">
          <Form.Item
            name="name"
            rules={[{ required: true, message: 'Please input your username!' }]}
          >
            <Input placeholder="" />
          </Form.Item>

          <Form.Item
            name="desc"
            rules={[{ required: true, message: 'Please input your password!' }]}
          >
            <TextArea rows={4} placeholder="" />
          </Form.Item>
          <Form.Item
            wrapperCol={{ span: 11 }}
            name="desc"
            rules={[{ required: true, message: 'Please input your password!' }]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item
            wrapperCol={{ span: 11 }}
            name="desc"
            rules={[{ required: true, message: 'Please input your password!' }]}
          >
            <Input placeholder="" />
          </Form.Item>

          <Form.Item>
            <CommonButton size="middle" emphasize btnClicked={getPrice} title="" />
          </Form.Item>
        </Form>
      </div>
      <p className="designer-copyright">
         <span className="designer-copyright-name"></span>{' '}
        
      </p>
    </div>
  )
}

export default WorkListDesignerInfo
