import './MenuFilter.less'
import SingleMenuFilter from './singleMenuFilter/SingleMenuFilter'
import {
  WORK_COLLECTION_CATEGORY,
  WORK_COLLECTION_NEW_WORK,
  WORK_COLLECTION_JUDGE,
  WORK_COLLECTION_PAGE_NUM,
} from '@/store/workCollectionSlice'
const list = [
  {
    key: 'all',
    label: '',
    val: '',
    actionName: WORK_COLLECTION_CATEGORY,
    children: [
      { key: 'all11', label: '', val: '' },
      { key: 'all12', label: '', val: '' },
      { key: 'all121', label: '', val: '' },
      { key: 'all122', label: '', val: '' },
      { key: 'all123', label: 'UI', val: 'UI' },
      { key: 'all124', label: '', val: '' },
      { key: 'all125', label: '', val: '' },
      { key: 'all126', label: '', val: '' },
      { key: 'all127', label: '', val: '' },
      { key: 'all128', label: '', val: '' },
      { key: 'all129', label: '', val: '' },
    ],
  },
  {
    key: 'newWorks',
    label: '',
    val: '',
    actionName: WORK_COLLECTION_NEW_WORK,
    children: [
      { key: 'newWorks1', label: '', val: '' },
      { key: 'newWorks2', label: '', val: '' },
      { key: 'newWorks3', label: '', val: '' },
      { key: 'newWorks4', label: '', val: '' },
    ],
  },
  {
    key: 'assessment',
    label: '',
    val: '',
    actionName: WORK_COLLECTION_JUDGE,
    children: [
      { key: 'assessment1', label: '', val: '' },
      { key: 'assessment2', label: '', val: '' },
      { key: 'assessment3', label: '', val: '' },
      { key: 'assessment4', label: '', val: '' },
    ],
  },
]

const MenuFilter = () => {
  return (
    <div className="menu-filter-container">
      {list.map((item) => (
        <SingleMenuFilter
          key={item.label}
          singleItem={item}
          actionName={item.actionName}
          actionPageName={WORK_COLLECTION_PAGE_NUM}
        />
      ))}
    </div>
  )
}
export default MenuFilter
